* {
  font-family: 'Gantari', sans-serif;
}
.loginIntro {
  font-size: 175%;
  color: aliceblue;
}
.webIntro {
  margin-top: 10%;
  font-size: 1.5rem;
}
.webIntroDiv {
  /* font-family: 'Gantari', sans-serif; */
  font-size: 170%;
  font-weight: 600;
  background-image: linear-gradient(45deg, #553c9a, #ee4b2b);
  color: transparent;
  background-clip: text;
  -webkit-background-clip: text;
}
.imgIntro {
  width: 60%;
  height: 40%;
}
.loginDiv {
  text-align: center;
  display: flex;
  min-height: 100vh;
  font-family: 'Jost', sans-serif;
  background-image: url('../../photos/profile.jpg');
  background-attachment: fixed;
  background-repeat: no-repeat;
  background-size: cover;
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  height: 100%;
  flex-direction: row;
}
.inputL {
  width: 100%;
  height: 20px;
  background: #e0dede;
  justify-content: center;
  display: flex;
  margin: 20px auto;
  padding: 10px;
  border: none;
  outline: none;
  border-radius: 5px;
}
.buttonl {
  /* width: 60%; */
  height: 23px;
  margin: 10px auto;
  justify-content: center;
  display: block;
  color: #fff;
  background: #573b8a;
  font-size: 1em;
  font-weight: bold;
  margin-top: 20px;
  outline: none;
  border: none;
  border-radius: 5px;
  transition: 0.2s ease-in;
  background: none;
  border: none;
  padding: 0;
  /*optional*/
  font-family: arial, sans-serif;
  text-decoration: underline;
  cursor: pointer;
}
.buttonl:hover {
  background: #6d44b8;
}
.login {
  text-align: center;
  width: 299px;
  background-color: white(255, 255, 255, 0.13);
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  border-radius: 10px;
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  border: 2px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 0 40px rgb(8 7 16 / 60%);
  padding: 50px 35px;
}
.hasAccount {
  color: wheat;
}
#loginBtn {
  margin-left: 8%;
  width: 90%;
  text-decoration: none;
  background-color: rgb(35, 85, 134);
  box-shadow: rgb(0 0 0 / 20%) 0px 2px 4px -1px,
    rgb(0 0 0 / 14%) 0px 4px 5px 0px, rgb(0 0 0 / 12%) 0px 1px 10px 0px;
}
#changePasswordBtn {
  margin-left: 8%;
  width: 90%;
  text-decoration: none;
  background-color: rgb(52, 165, 71);
  box-shadow: rgb(0 0 0 / 20%) 0px 2px 4px -1px,
    rgb(0 0 0 / 14%) 0px 4px 5px 0px, rgb(0 0 0 / 12%) 0px 1px 10px 0px;
}
#signUpBtn {
  /* padding: 6px 16px;
  margin-left: 8%; */
  width: 90%;
  text-decoration: none;
  background-color: rgb(52, 165, 71);
  box-shadow: rgb(0 0 0 / 20%) 0px 2px 4px -1px,
    rgb(0 0 0 / 14%) 0px 4px 5px 0px, rgb(0 0 0 / 12%) 0px 1px 10px 0px;
}
